.answer-option{

    width: 100%;
    height: 25%;
     
    display: flex;

    .container{

        width: 100%;
        height: 100%;
        
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        &.markGreen{
            background: #4caf50 !important ;
        }

        &.markRed{
            background: #d32f2f !important;
        }

        &:hover{
            background: #eee;
            cursor: pointer;
            
        }

        .answer-btn{
            width:15%;
            height: 100%;

            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;

            .custom-radio{
                
                border-radius: 50%;
                width:  40px;
                height: 40px;
               
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;

                &.dark{
                    background: purple;
                }
                &.light{
                    background: #ccc;
                }

                &.disable{
                    background: #ccc !important;

                    .check{
                        background: #ccc !important;
                    }
                }
                 

                .check{
                    width: 40%;
                    height: 40%;
                    border-radius: 50%;
                    background: #eee;
                }
            }
        }
    }
}

@media (max-width: 660px){

    .answer-option{

        .container{

            &:hover{
                background: #fff;
                cursor: pointer;
            }

            .answer-text{
                h5{
                    font-weight: 500;
                    font-size: 0.9rem;
                }
            }

            .answer-btn{

                    width: 30%;
                .custom-radio{
                    border-radius: 50%;
                    width:  27px;
                    height: 27px;

                   
                }
            }


        }
    }
}